import jokeApi from './js/joke_api';
import categoriesMarkup from './templates/categories_list.hbs';
import jokeMarkuphbs from './templates/joke_list.hbs';
import './styles.css';

const debounce = require('lodash.debounce');

//references for work with DOM elements

const refs = {
  jokesContainer: document.querySelector('#jokes'),
  searchBtn: document.querySelector('#search__btn'),
  jokesList: document.querySelector('#jokes__list'),
  randomSearch: document.querySelector('#random__search'),
  categorySearch: document.querySelector('#category__search'),
  searchByText: document.querySelector('#search_by_text'),
  categoriesList: document.querySelector('#categories__list'),
  searchInput: document.querySelector('#search__input'),
  jokesSection: document.querySelector('#jokes'),
  favouriteList: document.querySelector('#favourite__list'),
  openFavouriteButton: document.querySelector('#favourite__open-icon'),
  favouriteSection: document.querySelector('.favourite__list'),
  closeFavouriteButton: document.querySelector('#favourite__close-icon'),
  overlay: document.querySelector('.overlay'),
};

//all event listeners

refs.searchBtn.addEventListener('click', searchHandler);
refs.categorySearch.addEventListener('change', showCategories);
refs.randomSearch.addEventListener('change', hideCategories);
refs.searchByText.addEventListener('change', showInput);
refs.categoriesList.addEventListener('click', listClickHandler);
refs.searchInput.addEventListener('input', debounce(searchInputHandler, 500));
refs.jokesContainer.addEventListener('click', likeClickHandler);
refs.openFavouriteButton.addEventListener('click', openFavouriteHandler);
refs.closeFavouriteButton.addEventListener('click', closeFavouriteHandler);
window.addEventListener('load', renderFavouriteJoke);

//main function for work with API and radio buttons, use functions for
//generation mark up and insert to the page

function searchHandler(e) {
  e.preventDefault();
  if (refs.randomSearch.checked) {
    jokeApi.fetchRandomJoke().then(data => {
      const dataArr = [data];
      markupGen(dataArr);
    });
  } else if (refs.categorySearch.checked) {
    jokeApi.fetchCategoryJoke().then(data => markupGenCategory(data));
  } else if (refs.searchByText.checked) {
    jokeApi.fetchSearchJoke().then(data => {
      const rand = Math.floor(Math.random() * data.result.length);
      const dataArr = [data.result[rand]];
      markupGen(dataArr);
    });
  }
}

//getting a name of category and write to the API object property

function listClickHandler(e) {
  e.preventDefault();
  const categoryNameValue = e.target.textContent;

  jokeApi.categoryValue = categoryNameValue;
}

//getting a user query in free search and write to the API object property

function searchInputHandler(e) {
  e.preventDefault();
  const inputValue = e.target.value;
  jokeApi.inputQuery = inputValue;
}
//functions for show and hide category names and free search input
function showInput() {
  refs.searchInput.classList.remove('hide');
  refs.categoriesList.classList.add('hide');
  refs.categoriesList.classList.remove('flex');
  refs.searchInput.classList.add('flex');
}

function showCategories() {
  refs.searchInput.classList.remove('flex');
  refs.searchInput.classList.add('hide');
  refs.categoriesList.innerHTML = '';
  refs.categoriesList.classList.remove('hide');
  refs.categoriesList.classList.add('flex');

  jokeApi
    .fetchCategories()
    .then(data =>
      refs.categoriesList.insertAdjacentHTML(
        'beforeend',
        categoriesMarkup(data),
      ),
    );
}
function hideCategories() {
  refs.searchInput.classList.remove('flex');
  refs.categoriesList.classList.remove('flex');
  refs.searchInput.classList.add('hide');
  refs.categoriesList.classList.add('hide');
}

//functions for generation mark up and insert it to the page
function markupGen(data) {
  data.reduce((acc, joke) => {
    const li =
      acc +
      `<li class="jokes__list-item">
      <button class="jokes__like jokes__like-empty-heart" id="jokes__like"></button>
      <div class="jokes__container">
      <p class="jokes__id">ID:<a href="${joke.url}" target="_blank">${
        joke.id
      }</a></p>
      <p class="jokes__value">${joke.value}</p>
      <p class="jokes__update">Last update:${Math.round(
        (new Date() - new Date(joke.updated_at)) / 1000 / 60 / 60,
      )} hours ago</p>
      </div>
  </li>`;
    refs.jokesList.insertAdjacentHTML('beforeend', li);
  }, '');
}

function markupGenCategory(data) {
  const dataArr = [data];
  dataArr.reduce((acc, joke) => {
    const li =
      acc +
      `<li class="jokes__list-item">
      <button class="jokes__like jokes__like-empty-heart" id="jokes__like"></button>
      <div class="jokes__container">
      <p class="jokes__id">ID:<a href="${joke.url}" target="_blank">${
        joke.id
      }</a></p>
      <p class="jokes__value">${joke.value}</p>
      <div class="update_and_category">
      <p class="jokes__update">Last update:${Math.round(
        (new Date() - new Date(joke.updated_at)) / 1000 / 60 / 60,
      )} hours ago</p>
      <p class="jokes__category">${joke.categories}</p>
      </div>
      </div>
  </li>`;
    refs.jokesList.insertAdjacentHTML('beforeend', li);
  }, '');
}

//function for mark jokes as favourite, changing heart, gettind value from
//favourite joke, write it loke object property and use handelbars template for
//generation mark up, insert mark up to the document. Save favourite jokes to
//localStorge

let favouriteJokes = [];
let filteredJokes = [];
function likeClickHandler(e) {
  if (e.target.matches('.jokes__like')) {
    const IDValue = e.target.parentElement.children[1].children[0].innerText;
    const jokeValue = e.target.parentElement.children[1].children[1].innerText;
    const updateValue =
      e.target.parentElement.children[1].children[2].innerText;
    const jokesObj = {
      id: IDValue,
      value: jokeValue,
      updated_at: updateValue,
    };
    changeBG(e.target);
    favouriteJokes.push(jokesObj);
    jokeFilter(favouriteJokes, filteredJokes);

    insertMarkup(jokeMarkuphbs(filteredJokes));
    localStorage.setItem('favouriteJokesArr', JSON.stringify(filteredJokes));
  }
}
//array filter that compare id and push unique jokes to the new array
function jokeFilter(arrForFilter, filteredArr) {
  arrForFilter.forEach(joke => {
    let isPresent = filteredArr.find(
      filteredJoke => filteredJoke.id === joke.id,
    );
    if (!isPresent) {
      filteredArr.push(joke);
    }
  });
}

//function for cleaning list with favourite jokes before inserting new joke so
//joke in list don`t repeat

function insertMarkup(markUp) {
  refs.favouriteList.innerHTML = '';
  refs.favouriteList.insertAdjacentHTML('beforeend', markUp);
}

//changing background in like button after click
function changeBG(e) {
  e.classList.remove('jokes__like-empty-heart');
  e.classList.add('jokes__like-heart');
}

//getting favourite jokes from localStorage and insert to the favourite list in document
function renderFavouriteJoke() {
  const storage = JSON.parse(localStorage.getItem('favouriteJokesArr'));
  insertMarkup(jokeMarkuphbs(storage));
}
//opening and closing favourite list
function openFavouriteHandler() {
  refs.favouriteSection.classList.remove('hide');
  refs.overlay.classList.remove('hide');
}
function closeFavouriteHandler() {
  refs.favouriteSection.classList.add('hide');
  refs.overlay.classList.add('hide');
}
