const baseUrl = `https://api.chucknorris.io/jokes`;
const random = `/random`;
const categories = `/categories`;

export default {
  query: '',
  category: '',

  fetchRandomJoke() {
    return fetch(baseUrl + random).then(response => response.json());
  },
  fetchCategories() {
    return fetch(baseUrl + categories).then(response => response.json());
  },
  fetchCategoryJoke() {
    const categoryQuery = `/random?category=${this.category}`;
    return fetch(baseUrl + categoryQuery).then(response => response.json());
  },
  fetchSearchJoke() {
    const freeSearcQuery = `/search?query=${this.query}`;
    return fetch(baseUrl + freeSearcQuery).then(response => response.json());
  },
  set categoryValue(input) {
    this.category = input;
  },
  set inputQuery(input) {
    this.query = input;
  },
};
